import { SignInService } from './sign-in.service';
import { Component } from '@angular/core';
import {Http, Headers} from '@angular/http';
import {Observable} from 'rxjs';
@Component({
    selector: 'app-sign-in',
    template: `
    <form (ngSubmit)= "onSubmit(formSign)" #formSign ="ngForm" >
        <input 
        placeholder="Email" 
        ngModel #txtEmail="ngModel" 
        name="email" 
        required
        email>

        <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
            Email is required
        </p>
        <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
            Email is not valid
        </p>
        <br><br>
        <input 
            type="password" 
            placeholder="Password" 
            ngModel 
            #txtPassword="ngModel"
            name="password"
            minlength ="6"
            pattern="[a-z]*"
        >
        <br><br>
        <div ngModelGroup="subJect">
        <label><input type="checkbox" [ngModel]="false" name="nodeJS">Node JS</label> 
        <label><input type="checkbox" [ngModel]="false" name="Angular">Angular</label>
        <label><input type="checkbox" [ngModel]="false" name="ReactJS">ReactJS</label>
        </div>
        <br/>
        <button [disabled]="formSign.invalid">Submit</button>
    </form>
    <br/>
    <p>{{txtEmail.errors | json}}</p>
    <p>{{txtPassword.errors | json}}</p>
    <p>{{formSign.value | json}}</p>
    `,
    providers : [SignInService]
})
export class SignInComponent {
    constructor(private SignInService : SignInService){}
    onSubmit(formSign) {
        this.SignInService.sendPost(formSign.value)
        .then(result => console.log(result))
        .catch(err => console.log(err));

    }

}